<template>
  <!-- 登录 -->
  <div class="login">
    <div class="bg">
      <img src="~@/assets/images/login/join_bg.png" alt="" />
    </div>
    <div class="invite">好友喊您一起探索信纪元啦！</div>
    <div class="form">
      <Vcode :show="isVcodeShow" @success="onSuccess" @close="onClose" />
      <div class="inp-s1">
        <input type="tel" v-model="phone" placeholder="请输入手机号" />
      </div>
      <div class="inp-s1">
        <input type="text" v-model="code" placeholder="请输入手机验证码" />
        <a v-if="countdown <= 0" class="sms-btn" @click="showVcode"
          >获取验证码</a
        >
        <span v-else class="sms-btn disabled">{{ countdown }}s后获取</span>
      </div>
      <div class="btn-s1" @click="submit">
        <a class="btn">立即参与</a>
      </div>
    </div>
    <Modal class="modal" :visible.sync="isModalShow" :title="modalTitle">
      <div class="modal-content" slot="content">
        <div class="text mb-24">{{ contentText }}</div>
        <div v-if="contentSubText" class="sub-text mb-32">
          {{ contentSubText }}
        </div>
      </div>
      <div class="modal-footer" slot="footer">
        <img
          @click="goHome"
          src="@/assets/images/common/modal_btn_ljqw.png"
          alt=""
        />
        <div class="gray-text" v-if="loginResult && loginResult.data === '2'">
          仅支持该手机号参与领取
        </div>
      </div>
    </Modal>
  </div>
</template>

<script>
import Vcode from 'vue-puzzle-vcode'
import { utils } from '@/utils/utils'

import Modal from '@/components/commonModal'

export default {
  name: 'Login',
  data () {
    return {
      isVcodeShow: false,
      phone: '',
      code: '',
      btnLoading: false,
      countdown: 0,
      isModalShow: false,
      modalTitle: '提示', // 弹窗顶部文字
      contentText: '', // 弹窗内容
      contentSubText: '', // 弹窗副内容
      loginResult: null
    }
  },
  components: {
    Vcode,
    Modal
  },
  created () {
    console.log(this.$route)
  },
  methods: {
    // 展示滑动验证
    showVcode () {
      console.log(utils)
      if (!utils.telReg.test(this.phone)) {
        this.Toast('请输入正确的手机号')
        return
      }
      this.isVcodeShow = true
    },
    // 滑动验证通过
    onSuccess (msg) {
      this.isVcodeShow = false // 通过验证后，需要手动关闭模态框
      this.sendRequest()
    },
    // 关闭滑动验证
    onClose () {
      this.isVcodeShow = false
    },
    // 发送验证码
    async sendRequest () {
      await this.$request('post', '/register/sendsmscode', {
        mobile: this.phone,
        validate: ''
      })
      this.Toast(`验证码发送成功`)
      this.countdown = 60
      let timer = setInterval(() => {
        this.countdown--
        if (this.countdown <= 0) {
          clearInterval(timer)
        }
      }, 1000)
    },
    async submit () {
      if (!utils.telReg.test(this.phone)) {
        this.Toast('请输入正确的手机号')
        return
      }
      if (!/^\d{4,6}$/.test(this.code)) {
        this.Toast('请输入手机验证码')
        return
      }
      this.btnLoading = true
      const res = await this.$request('post', '/register/join', {
        mobile: this.phone,
        sms_code: this.code,
        invite_code: this.$route.query.invite_code || '', // 邀请码（从链接中获取）
        sid: this.$route.query.sid || '' // 赠送星钻标识（从链接中获取）
      }).finally(() => {
        this.btnLoading = false
      })
      this.loginResult = res
      if (res.data === '1') {
        this.modalTitle = '您已参与活动'
        this.contentText = '无法接受邀请'
        this.contentSubText = '继续参与活动赢好礼'
      } else if (res.data === '2') {
        this.modalTitle = '欢迎新朋友'
        this.contentText = '参与活动才可受邀成功哦'
        this.contentSubText = '立即前往信e投APP'
      } else if (res.data === '3') {
        this.modalTitle = '您已经被邀请过了'
        this.contentText = '赶紧去参与活动吧'
        this.contentSubText = ''
      } else if (res.data === '4') {
        this.modalTitle = '恭喜您'
        this.contentText = '已成功领取好友赠送星钻'
        this.contentSubText = '请前往APP活动页查看'
      } else if (res.data === '5') {
        this.modalTitle = '来晚啦'
        this.contentText = '星钻奖励已被领走了'
        this.contentSubText = '继续参与活动赢好礼'
      }
      this.isModalShow = true
      this.$store.commit('setUserInfo', res.data)
    },
    goHome () {
      this.$router.replace('home')
    }
  }
}
</script>

<style lang="less" scoped>
.login {
  background-color: #e94b4d;
  // min-height: calc(100vh - 8vw);
  min-height: 100vh;
  .bg {
    img {
      width: 100%;
    }
  }
  .invite {
    margin-top: 30px;
    font-size: 30px;
    color: #f2cf8d;
    text-align: center;
  }
  .form {
    padding: 35px 50px;
    .inp-s1 {
      background-image: url(~@/assets/images/login/join_shuru.png);
      background-size: 100%;
      height: 90px;
      margin-bottom: 25px;
      display: flex;
      padding: 16px 38px;
      align-items: center;
      input {
        border: none;
        outline: none;
        background-color: transparent;
        width: 100%;
        height: 100%;
        box-sizing: border-box;
        font-size: 30px;
        color: #333;
      }
      .sms-btn {
        font-size: 30px;
        color: #e94c4e;
        white-space: nowrap;
        text-decoration: none;
        &.disabled {
          color: #999;
        }
      }
    }
    .btn-s1 {
      background-image: url(~@/assets/images/login/join_btn.png);
      background-size: 100%;
      height: 97px;
      display: flex;
      align-items: center;
      justify-content: center;
      margin-top: 30px;
      a {
        font-size: 34px;
        color: #b02228;
        font-weight: bold;
        margin-top: -6px;
      }
    }
  }
  .gray-text {
    color: #666;
    font-size: 24px;
  }
}
</style>



